.btnPopupWrap {
	position: fixed;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(24, 24, 27, .90);
	display: flex;
	justify-content: center;
	align-items: center;
	z-index: 10000;

	.popupCloseBtn {
		width: 46px;
		height: 46px;
		position: absolute;
		top: 0;
		right: 0;
		display: inline-flex;
		align-items: center;
		justify-content: center;
		cursor: pointer;
		background: none;
		padding: 0;
		border: none;

		&:hover {
			background: #464649a6;
		}
	}

	.popupContent {
		max-width: calc(100vw - 60px);
		max-height: calc(100vh - 120px);
		position: relative;
		text-align: initial;
		background-color: #fff;
		margin: 0;
		border-radius: 7px;
		box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);

		.image,
		.content,
		.document,
		.iframe {
			max-width: calc(100vw - 60px);
			max-height: calc(100vh - 120px);
		}

		.image {
			object-fit: contain;
		}

		.content {
			width: 960px;
			padding: 35px;
			overflow: auto;
		}

		.plyr {
			display: block;

			video {
				max-width: calc(100vw - 60px);
				height: calc(100vh - 120px);
				max-height: calc(100vh - 120px);
				display: block;
			}

			input[type=range]::-webkit-slider-runnable-track {
				box-shadow: none;
			}

			input[type=range]::-moz-range-track {
				box-shadow: none;
			}

			input[type=range]::-ms-track {
				box-shadow: none;
			}
		}

		.document,
		.iframe {
			width: 960px;
			height: calc(100vh - 120px);
			overflow: auto;

			iframe {
				width: 100%;
				height: 100%;
				display: block;
			}
		}

		.iframe {
			overflow: hidden;
		}

		.warning {
			font-size: 15px;
			font-weight: bold;
			text-transform: capitalize;
			color: #32373c;
			background-color: #fff8e5;
			padding: 12px;
			border-left: 4px solid #ffb900;
			margin: 0 !important;
		}
	}

	.popupCaption {
		font-size: 16px;
		text-align: center;
		color: #fff;
		display: inline-block;
		position: absolute;
		bottom: 0;
		left: 50%;
		transform: translate(-50%, calc(100% + 10px));
		margin: 0 !important;
	}
}